.divider {
  margin: 5px 0;
  border-style: solid;
  border-bottom: 0;
}

/*
 * Theme
 */

:global(.light) {
  .divider {
    border-color: $light_menuDivider_color;
  }
}

:global(.dark) {
  .divider {
    border-color: $dark_menuDivider_color;
  }
}



