.sponsor {
  padding: 5px 0;
  box-sizing: border-box;
  position: relative;

  strong {
    margin: 5px 0;
    display: block;
  }

  strong,
  .description {
    line-height: 1.4;
  }
}


.sponsorText {
  position: absolute;
  top: -15px;
  padding: 4px 6px;
  border-radius: 3px;
  content: "Sponsor";
  text-transform: uppercase;
  font-weight: bold;
  font-size: 0.7rem;
}

:global(.light) {
  .sponsorText {
    background: $light_theme_color;
    color: $light_lightGrey;
  }
}

:global(.dark) {
  .sponsorText {
    background: $dark_theme_color;
    color: $dark_globalApp_color;
  }
}



