.listItem {
  @mixin textOverflow;

  display: flex;
  flex-direction: row;
  align-items: center;
  overflow: hidden;
  text-transform: lowercase;
  line-height: 1.25;
  cursor: pointer;
}

.disabled {
  opacity: 0.3;
  pointer-events: none;

  &, * {
    /* Reset styles when disabled */
    color: inherit !important;
    font-weight: inherit !important;
  }
}



