.root {
  min-width: 100px;
  font-family: $codeFont;
  line-height: 1.3;
}

.match {
  margin-bottom: 5px;
  padding-bottom: 5px;
  border-bottom: 1px solid;
}

.group {
  white-space: pre;
}

.group span,
.match span {
  font-weight: bold;
}

/*
 * Theme
 */

:global(.light) {
  .match {
    border-bottom-color: $light_testareaTooltip_match_borderColor;

    span {
      color: $light_testareaTooltip_matchNumber_color;
    }
  }
  .group span {
    color: $light_testareaTooltip_groupNumber_color;
  }
}

:global(.dark) {
  .match {
    border-bottom-color: $dark_testareaTooltip_match_borderColor;

    span {
      color: $dark_testareaTooltip_matchNumber_color;
    }
  }
  .group span {
    color: $dark_testareaTooltip_groupNumber_color;
  }
}



