.copyBtn {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

/*
 * Theme
 */

:global(.light) {
  .icon {
    color: $light_copyButton_color;
  }
}

:global(.dark) {
  .icon {
    color: $dark_copyButton_color;
  }
}



