.root {
  padding: 10px;
  border-radius: 3px;
}

.tagList {
  margin-top: 5px;
}

/*
 * Theme
*/

:global(.light) {
  .root:nth-child(odd) {
    background: $light_userDataItem_background;
  }
}

:global(.dark) {
  .root:nth-child(odd) {
    background: $dark_userDataItem_background;
  }
}




