.matchData {
  @mixin wordwrap;
}

.didNotParticipate {
  font-style: italic;
  opacity: 0.5;
}

.groupNum {
  padding: 5px 0;

  &.clickable {
    cursor: pointer;
  }

  span {
    padding: 2px 3px;
  }
}

.matchPos {
  padding: 5px 10px;
}

.groupNum,
.shrink {
  white-space: nowrap;
}

/*
 * THeme
 */

:global(.light) {
  .matchPos {
    color: $light_matchGroup_pos_color;
  }

  .groupNum.clickable:hover span {
    background: $light_codeMirror_highlight !important;
  }
}

:global(.dark) {
  .matchPos {
    color: $dark_matchGroup_pos_color;
  }

  .groupNum.clickable:hover span {
    background: $dark_codeMirror_highlight !important;
  }
}



