.pageArrow {
  @mixin genericButton;

  font-size: 0.7rem;

  &.hidden {
    visibility: hidden;
  }
}

/*
 * Theme
 */

:global(.light) {
  .pageArrow {
    color: $light_pageArrow_color;
  }
}

:global(.dark) {
  .pageArrow {
    color: $dark_pageArrow_color;
  }
}



