.header {
  padding: 8px 20px;
  text-transform: uppercase;
  font-weight: bold;
}

/*
 * Theme
 */

:global(.light) {
  .header {
    color: $light_menuHeader_color;
  }
}

:global(.dark) {
  .header {
    color: $dark_menuHeader_color;
  }
}



