.contentArea {
  /*
   We want 10px between the elements, thats 20px of vertical space lost in total.
   20/3 = 6.6667 ~7px
   */
  flex-grow: 1;
  flex-shrink: 0;
  border: 1px solid;
  border-radius: 3px;
  font-size: 0.85rem;
  transition: flex $testareaSpeed $motionEasing;

}

.two {
  flex-basis: calc(50% - 10px);
}

.three {
  flex-basis: calc(33% - 7px);
}

.titleBtn {
  margin: 0 10px;
  padding: 10px 0 4px 0;
}

.content > * {
  @mixin fullscreen 7px, 10px, 10px, 10px;
}

.shrinkArea {
  flex: 0 1 34px;

  .titleBtn {
    margin: 0 10px;
    padding: 10px 0;
    border: 0;
  }
}

.resizable {
  margin-bottom: 10px;
}

/*
 * Theme
 */

:global(.light) {
  .contentArea {
    border-color: $light_rightSidebar_contentArea_borderColor;
    background: $light_rightSidebar_contentArea_background;
  }
}

:global(.dark) {
  .contentArea {
    border-color: $dark_rightSidebar_contentArea_borderColor;
    background: $dark_rightSidebar_contentArea_background;
  }
}



