.toggleItem {
  display: flex;
  flex: 1 1 auto;
}

.checkMark {
  flex: 1 0 auto;
  padding-left: 5px;
  text-align: right;
  line-height: 1.25;
}

.isActive {
  font-weight: bold;
}

/*
 * Theme
 */

:global(.light) {
  .toggleItem:not(:active) .checkMark {
    color: $light_listMenu_toggleContent_checkMark_color;
  }
}

:global(.dark) {
  .toggleItem:not(:active) .checkMark {
    color: $dark_listMenu_toggleContent_checkMark_color;
  }
}



