.inputArea {
  display: flex;
  flex: 1 1 auto;
  flex-direction: column;
  overflow: hidden;
  height: 100%;

  :global {
    .CodeMirror pre {
      padding: 0 2px; /* Horizontal padding of content, needed for borders to not clip when hovering */
    }

    .CodeMirror-lines {
      padding: 2px 0; /* Horizontal padding of content, needed for borders to not clip when hovering */
    }
  }
}

.result {
  @mixin wordwrap;

  flex: 1 1 auto;
  overflow: auto;
  line-height: 1.4em;
}

.input {
  flex: 0 0 auto;
  margin-bottom: 5px;
  padding: 8px;
}

.input,
.result {
  @mixin box;
  @mixin inputArea;

  outline: 0;
  font-size: 0.875rem;
  font-family: $codeFont;
  resize: none;
}

.result {
  position: relative;
  width: 100%;
  height: 100%;

  :global(.CodeMirror) {
    @mixin fullscreen 5px, 5px, 5px, 5px;
  }
}

/*
 * Theme
 */

:global(.light) {
  .result {
    border-color: $light_textInput_borderColor;
    background: $light_textInput_disabled_background;
  }
  .input {
    border-color: $light_textInput_borderColor;
    background: $light_textInput_background;

    &.editorFocused {
      border-color: $light_textInput_activeBorderColor;
      box-shadow: $light_textInput_activeBoxShadow;
    }
  }
}

:global(.dark) {
  .result {
    border-color: $dark_textInput_borderColor;
    background: $dark_textInput_disabled_background;
  }
  .input {
    border-color: $dark_textInput_borderColor;
    background: $dark_textInput_background;

    &.editorFocused {
      border-color: $dark_textInput_activeBorderColor;
      box-shadow: $dark_textInput_activeBoxShadow;
    }
  }
}



