/* Import all global css files here to have them nice and tidy in one place */
@import 'reset.css';
@import 'font.css';
@import 'RegexApp.css';

.root {
  width: 100%;
  height: 100%;
}

.contentArea {
  @mixin fullscreen $headerHeight, 0, 0, $totalLeftSidebarWidth;

  &.alwaysCollapseLeftSidebar {
    left: 0;
  }
}

@media (max-width: $leftSidebarBreakpoint) {
  .contentArea {
    left: 0;
  }
}

/*
 * Theme
 */

:global(.light) {
  .root {
    background: $light_globalApp_background;
    color: $light_globalApp_color;
  }
}

:global(.dark) {
  .root {
    background: $dark_globalApp_background;
    color: $dark_globalApp_color;
  }
}



