.markdownContainer {
  overflow: auto;
  padding-bottom: 5px;
  white-space: pre;

  > *:first-child {
    margin-top: 0;
  }
  ul,
  ol {
    padding-left: 30px;
  }
  ul {
    list-style-type: disc;
  }
  ol {
    list-style-type: decimal;
  }
  p {
    margin: 10px 0;
  }
  h1,
  h2 {
    padding-bottom: 5px;
    border-bottom: 1px solid;
  }
  h1 {
    font-size: 1.5rem;
  }
  h2 {
    font-size: 1.2rem;
  }
  h3 {
    font-size: 1rem;
  }
  h4 {
    font-size: 0.8rem;
  }
  h5 {
    font-size: 0.6rem;
  }
  h6 {
    font-size: 0.5rem;
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin-bottom: 20px;
  }
  code {
    display: inline;
    padding: 2px;
    border-radius: 3px;
    white-space: pre;
    font-family: $codeFont;
  }
  pre {
    display: block;
    overflow: auto;
    padding: 16px;
    border-radius: 3px;
    white-space: normal;
    font-family: $codeFont;
    line-height: 1.45;

    code {
      padding: 0;
      background: none;
    }
  }
  blockquote {
    padding: 0 15px;
    border-left: 4px solid;
  }
  hr {
    margin: 24px 0;
    padding: 0;
    height: 4px;
    border: 0;
  }
}

/*
 * Theme
 */

:global(.light) {
  .markdownContainer {
    h1,
    h2 {
      border-color: $light_markdownContainer_header_borderColor;
    }
    code {
      background: $light_markdownContainer_code_background;
    }
    pre {
      background: $light_markdownContainer_pre_background;
    }
    blockquote {
      border-left-color: $light_markdownContainer_blockQuote_borderColor;
      color: $light_markdownContainer_blockQuote_color;
    }
    hr {
      background: $light_markdownContainer_hr_background;
    }
  }
}

:global(.dark) {
  .markdownContainer {
    h1,
    h2 {
      border-color: $dark_markdownContainer_header_borderColor;
    }
    code {
      background: $dark_markdownContainer_code_background;
    }
    pre {
      background: $dark_markdownContainer_pre_background;
    }
    blockquote {
      border-left-color: $dark_markdownContainer_blockQuote_borderColor;
      color: $dark_markdownContainer_blockQuote_color;
    }
    hr {
      background: $dark_markdownContainer_hr_background;
    }
  }
}



