.root {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  padding: 10px;
  width: 100%;
  border-radius: 3px;
}

.regex {
  @mixin wordwrap;

  flex: 0 0 auto;
  padding-right: 10px;
  max-width: 35%;
}

.lineNumber {
  flex: 0 0 auto;
  padding-right: 10px;
  text-align: right;
}

.stringMatch,
.regexHighlight {
  padding: 0 2px;
}

.backtrackIcon {
  float: right;
  margin-left: 10px;
  padding: 0 5px;
  cursor: help;
}

.stringArea {
  @mixin wordwrap;

  flex: 1 1 auto;
}

.string,
.backtrackIcon {
  vertical-align: top;
}

/*
 * Theme
 */

:global(.light) {
  .root:nth-child(odd) {
    background: $light_debugStep_background;
  }
  .lineNumber {
    color: $light_debugStep_lineNumber_color;
  }
  .regexHighlight {
    background: $light_debugStep_regexHighlight_background;
    color: $light_debugStep_regexHighlight_color;
  }
  .regex {
    color: $light_debugStep_regex_color;
  }
  .backtrackIcon {
    color: $light_debugStep_backtrackIcon_color;
  }
  .group0 {
    background: $light_matchGroup_0;
  },
  .group02 {
    background: $light_matchGroup_0_alt;
  }
}

:global(.dark) {
  .root:nth-child(odd) {
    background: $dark_debugStep_background;
  }
  .lineNumber {
    color: $dark_debugStep_lineNumber_color;
  }
  .regexHighlight {
    background: $dark_debugStep_regexHighlight_background;
    color: $dark_debugStep_regexHighlight_color;
  }
  .regex {
    color: $dark_debugStep_regex_color;
  }
  .backtrackIcon {
    color: $dark_debugStep_backtrackIcon_color;
  }
  .group0 {
    background: $dark_matchGroup_0;
  },
  .group02 {
    background: $dark_matchGroup_0_alt;
  }
}



