.groupLabel,
.tokenExplanation {
  display: inline-block;
}

.groupLabel,
.nodeLabel,
.boldNodeLabel {
  @mixin wordwrap;

  vertical-align: top;
}

.boldNodeLabel {
  font-weight: bold;
}

.groupLabel,
.token {
  letter-spacing: 1px;
  font-family: $codeFont;
}

.token {
  @mixin wordwrap;
}



