:global {
  html,
  body {
    font-family: $appFont;
    overflow: hidden;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    -webkit-tap-highlight-color: transparent;
    text-size-adjust: 100%;
  }
  #regex-app {
    width: 100%;
    height: 100%;
  }
  sub {
    vertical-align: sub;
    font-weight: normal;
    font-size: 0.5rem;
  }
  /* Required for textareas with transparent text in firefox */
  ::selection {
    background: rgba(77, 145, 226, 0.3);
  }
  ::placeholder {
    opacity: 1;
  }
  strong {
    font-weight: bold;
  }
  em {
    font-style: italic;
  }
  a {
    color: inherit;
    text-decoration: none;
  }
  .light ::placeholder {
    color: $light_textInput_placeholder_color;
  }
  .dark ::placeholder {
    color: $dark_textInput_placeholder_color;
  }
}



