.root {
  @mixin fullscreen;
}

.regexPlayground {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  box-sizing: border-box;
  padding: 10px;
  height: 100%;
}

.switchableArea {
  position: relative;
  flex: 1 1 auto;
  overflow: hidden;
}



