.root {
  display: flex;
  flex: 0 1 auto;
  flex-direction: column;
  margin-top: 10px;
  margin-bottom: -7px; /* Label adds 7px bottom margin, remove this when not visible */
  transition: flex $testareaSpeed $motionEasing;

  &.visible {
    flex: 1 1 auto;
    margin-bottom: 0;
  }
}

@media (max-height: $substitutionAreaBreakpoint) {
  .root.visible {
    margin-top: 0;
  }
}



